import ol from 'openlayers'

import {TRIANGLEFLAG} from '../../Utils/PlotTypes'
class TriangleFlag extends ol.geom.Polygon {
  constructor (coordinates, points, params) {
    super([])
    this.type = TRIANGLEFLAG
    this.fixPointCount = 2
    this.set('params', params)
    if (points && points.length > 0) {
      this.setPoints(points)
    } else if (coordinates && coordinates.length > 0) {
      this.setCoordinates(coordinates)
    }
  }

  /**
   * 
   * @returns {*}
   */
  getPlotType () {
    return this.type
  }

  /**
   * 
   */
  generate () {
    let count = this.getPointCount()
    if (count < 2) {
      return false
    } else {
      this.setCoordinates([this.calculatePonits(this.points)])
    }
  }

  /**
   * 
   * @param points
   * @returns {Array}
   */
  calculatePonits (points) {
    let components = []
    // 
    if (points.length > 1) {
      // 
      let startPoint = points[0]
      // 
      let endPoint = points[points.length - 1]
      var point1 = [endPoint[0], (startPoint[1] + endPoint[1]) / 2]
      var point2 = [startPoint[0], (startPoint[1] + endPoint[1]) / 2]
      var point3 = [startPoint[0], endPoint[1]]
      components = [startPoint, point1, point2, point3]
    }
    return components
  }

  /**
   * 
   * @param map
   */
  setMap (map) {
    if (map && map instanceof ol.Map) {
      this.map = map
    } else {
      throw new Error('')
    }
  }

  /**
   * 
   * @returns {ol.Map|*}
   */
  getMap () {
    return this.map
  }

  /**
   * Plot
   * @returns {boolean}
   */
  isPlot () {
    return true
  }

  /**
   * 
   * @param value
   */
  setPoints (value) {
    this.points = !value ? [] : value
    if (this.points.length >= 1) {
      this.generate()
    }
  }

  /**
   * 
   * @returns {Array.<T>}
   */
  getPoints () {
    return this.points.slice(0)
  }

  /**
   * 
   * @returns {Number}
   */
  getPointCount () {
    return this.points.length
  }

  /**
   * 
   * @param point
   * @param index
   */
  updatePoint (point, index) {
    if (index >= 0 && index < this.points.length) {
      this.points[index] = point
      this.generate()
    }
  }

  /**
   * 
   * @param point
   */
  updateLastPoint (point) {
    this.updatePoint(point, this.points.length - 1)
  }

  /**
   * 
   */
  finishDrawing () {
  }
}

export default TriangleFlag



