/**
 * Created by FDD on 2017/5/24.
 * @desc 
 * @Inherits ol.geom.Polygon
 */
import ol from 'openlayers'
import {RECTANGLE} from '../../Utils/PlotTypes'
class RectAngle extends ol.geom.Polygon {
  constructor (coordinates, points, params) {
    super([])
    this.type = RECTANGLE
    this.fixPointCount = 2
    this.set('params', params)
    this.isFill = ((params['isFill'] === false) ? params['isFill'] : true)
    if (points && points.length > 0) {
      this.setPoints(points)
    } else if (coordinates && coordinates.length > 0) {
      this.setCoordinates(coordinates)
    }
  }

  /**
   * 
   * @returns {*}
   */
  getPlotType () {
    return this.type
  }

  /**
   * 
   */
  generate () {
    if (this.points.length === 2) {
      let coordinates = []
      if (this.isFill) {
        let extent = ol.extent.boundingExtent(this.points)
        let polygon = ol.geom.Polygon.fromExtent(extent)
        coordinates = polygon.getCoordinates()
      } else {
        let start = this.points[0]
        let end = this.points[1]
        coordinates = [start, [start[0], end[1]], end, [end[0], start[1]], start]
      }
      this.setCoordinates(coordinates)
    }
  }

  /**
   * 
   * @param map
   */
  setMap (map) {
    if (map && map instanceof ol.Map) {
      this.map = map
    } else {
      throw new Error('')
    }
  }

  /**
   * 
   * @returns {ol.Map|*}
   */
  getMap () {
    return this.map
  }

  /**
   * Plot
   * @returns {boolean}
   */
  isPlot () {
    return true
  }

  /**
   * 
   * @param value
   */
  setPoints (value) {
    this.points = !value ? [] : value
    if (this.points.length >= 1) {
      this.generate()
    }
  }

  /**
   * 
   * @returns {Array.<T>}
   */
  getPoints () {
    return this.points.slice(0)
  }

  /**
   * 
   * @returns {Number}
   */
  getPointCount () {
    return this.points.length
  }

  /**
   * 
   * @param point
   * @param index
   */
  updatePoint (point, index) {
    if (index >= 0 && index < this.points.length) {
      this.points[index] = point
      this.generate()
    }
  }

  /**
   * 
   * @param point
   */
  updateLastPoint (point) {
    this.updatePoint(point, this.points.length - 1)
  }

  /**
   * 
   */
  finishDrawing () {
  }
}

export default RectAngle



