import ol from 'openlayers'
import {CIRCLE} from '../../Utils/PlotTypes'
import * as PlotUtils from '../../Utils/utils'
class Circle extends ol.geom.Polygon {
  constructor (coordinates, points, params) {
    super([])
    this.type = CIRCLE
    this.fixPointCount = 2
    this.set('params', params)
    if (points && points.length > 0) {
      this.setPoints(points)
    } else if (coordinates && coordinates.length > 0) {
      this.setCoordinates(coordinates)
    }
  }

  /**
   * 
   * @returns {*}
   */
  getPlotType () {
    return this.type
  }

  generate () {
    let count = this.getPointCount()
    if (count < 2) {
      return false
    } else {
      let center = this.points[0]
      let radius = PlotUtils.MathDistance(center, this.points[1])
      this.setCoordinates([this.generatePoints(center, radius)])
    }
  }

  /**
   * 
   * @param center
   * @param radius
   * @returns {null}
   */
  generatePoints (center, radius) {
    let [x, y, angle, points] = [null, null, null, []]
    for (let i = 0; i <= 100; i++) {
      angle = Math.PI * 2 * i / 100
      x = center[0] + radius * Math.cos(angle)
      y = center[1] + radius * Math.sin(angle)
      points.push([x, y])
    }
    return points
  }

  /**
   * 
   * @param map
   */
  setMap (map) {
    if (map && map instanceof ol.Map) {
      this.map = map
    } else {
      throw new Error('')
    }
  }

  /**
   * 
   * @returns {{}|*}
   */
  getMap () {
    return this.map
  }

  /**
   * Plot
   * @returns {boolean}
   */
  isPlot () {
    return true
  }

  /**
   * 
   * @param value
   */
  setPoints (value) {
    this.points = !value ? [] : value
    if (this.points.length >= 1) {
      this.generate()
    }
  }

  /**
   * 
   * @returns {Array.<T>}
   */
  getPoints () {
    return this.points.slice(0)
  }

  /**
   * 
   * @returns {Number}
   */
  getPointCount () {
    return this.points.length
  }

  /**
   * 
   * @param point
   * @param index
   */
  updatePoint (point, index) {
    if (index >= 0 && index < this.points.length) {
      this.points[index] = point
      this.generate()
    }
  }

  /**
   * 
   * @param point
   */
  updateLastPoint (point) {
    this.updatePoint(point, this.points.length - 1)
  }

  /**
   * 
   */
  finishDrawing () {
  }
}

export default Circle



