import ol from 'openlayers'

import {ELLIPSE} from '../../Utils/PlotTypes'
import * as Constants from '../../Constants'
import * as PlotUtils from '../../Utils/utils'
class Ellipse extends ol.geom.Polygon {
  constructor (coordinates, points, params) {
    super([])
    this.type = ELLIPSE
    this.fixPointCount = 2
    this.set('params', params)
    if (points && points.length > 0) {
      this.setPoints(points)
    } else if (coordinates && coordinates.length > 0) {
      this.setCoordinates(coordinates)
    }
  }

  /**
   * 
   * @returns {*}
   */
  getPlotType () {
    return this.type
  }

  generate () {
    if (this.getPointCount() < 2) {
      return false
    } else {
      let [pnt1, pnt2] = [this.points[0], this.points[1]]
      let center = PlotUtils.Mid(pnt1, pnt2)
      let majorRadius = Math.abs((pnt1[0] - pnt2[0]) / 2)
      let minorRadius = Math.abs((pnt1[1] - pnt2[1]) / 2)
      let res = this.generatePoints(center, majorRadius, minorRadius)
      this.setCoordinates([res])
    }
  }

  /**
   * 
   * @param center
   * @param majorRadius
   * @param minorRadius
   * @returns {*}
   */
  generatePoints (center, majorRadius, minorRadius) {
    let [x, y, angle, points] = [null, null, null, []]
    for (let i = 0; i <= Constants.FITTING_COUNT; i++) {
      angle = Math.PI * 2 * i / Constants.FITTING_COUNT
      x = center[0] + majorRadius * Math.cos(angle)
      y = center[1] + minorRadius * Math.sin(angle)
      points.push([x, y])
    }
    return points
  }

  /**
   * 
   * @param map
   */
  setMap (map) {
    if (map && map instanceof ol.Map) {
      this.map = map
    } else {
      throw new Error('')
    }
  }

  /**
   * 
   * @returns {{}|*}
   */
  getMap () {
    return this.map
  }

  /**
   * Plot
   * @returns {boolean}
   */
  isPlot () {
    return true
  }

  /**
   * 
   * @param value
   */
  setPoints (value) {
    this.points = !value ? [] : value
    if (this.points.length >= 1) {
      this.generate()
    }
  }

  /**
   * 
   * @returns {Array.<T>}
   */
  getPoints () {
    return this.points.slice(0)
  }

  /**
   * 
   * @returns {Number}
   */
  getPointCount () {
    return this.points.length
  }

  /**
   * 
   * @param point
   * @param index
   */
  updatePoint (point, index) {
    if (index >= 0 && index < this.points.length) {
      this.points[index] = point
      this.generate()
    }
  }

  /**
   * 
   * @param point
   */
  updateLastPoint (point) {
    this.updatePoint(point, this.points.length - 1)
  }

  /**
   * 
   */
  finishDrawing () {
  }
}

export default Ellipse



