import ol from 'openlayers'
import {ARC} from '../../Utils/PlotTypes'
import * as PlotUtils from '../../Utils/utils'
class Arc extends ol.geom.LineString {
  constructor (coordinates, points, params) {
    super([])
    this.type = ARC
    this.fixPointCount = 3
    this.set('params', params)
    if (points && points.length > 0) {
      this.setPoints(points)
    } else if (coordinates && coordinates.length > 0) {
      this.setCoordinates(coordinates)
    }
  }

  /**
   * 
   * @returns {*}
   */
  getPlotType () {
    return this.type
  }

  /**
   * 
   */
  generate () {
    let count = this.getPointCount()
    if (count < 2) return
    if (count === 2) {
      this.setCoordinates(this.points)
    } else {
      let [pnt1, pnt2, pnt3, startAngle, endAngle] = [this.points[0], this.points[1], this.points[2], null, null]
      let center = PlotUtils.getCircleCenterOfThreePoints(pnt1, pnt2, pnt3)
      let radius = PlotUtils.MathDistance(pnt1, center)
      let angle1 = PlotUtils.getAzimuth(pnt1, center)
      let angle2 = PlotUtils.getAzimuth(pnt2, center)
      if (PlotUtils.isClockWise(pnt1, pnt2, pnt3)) {
        startAngle = angle2
        endAngle = angle1
      } else {
        startAngle = angle1
        endAngle = angle2
      }
      this.setCoordinates(PlotUtils.getArcPoints(center, radius, startAngle, endAngle))
    }
  }

  /**
   * 
   * @param map
   */
  setMap (map) {
    if (map && map instanceof ol.Map) {
      this.map = map
    } else {
      throw new Error('')
    }
  }

  /**
   * 
   * @returns {ol.Map|*}
   */
  getMap () {
    return this.map
  }

  /**
   * Plot
   * @returns {boolean}
   */
  isPlot () {
    return true
  }

  /**
   * 
   * @param value
   */
  setPoints (value) {
    this.points = !value ? [] : value
    if (this.points.length >= 1) {
      this.generate()
    }
  }

  /**
   * 
   * @returns {Array.<T>}
   */
  getPoints () {
    return this.points.slice(0)
  }

  /**
   * 
   * @returns {Number}
   */
  getPointCount () {
    return this.points.length
  }

  /**
   * 
   * @param point
   * @param index
   */
  updatePoint (point, index) {
    if (index >= 0 && index < this.points.length) {
      this.points[index] = point
      this.generate()
    }
  }

  /**
   * 
   * @param point
   */
  updateLastPoint (point) {
    this.updatePoint(point, this.points.length - 1)
  }

  /**
   * 
   */
  finishDrawing () {
  }
}

export default Arc



