/**
 * Created by FDD on 2017/5/25.
 * @desc 
 * @Inherits ol.geom.Polygon
 */
import ol from 'openlayers'
import {SECTOR} from '../../Utils/PlotTypes'
import * as PlotUtils from '../../Utils/utils'
class Sector extends ol.geom.Polygon {
  constructor (coordinates, points, params) {
    super([])
    this.type = SECTOR
    this.fixPointCount = 3
    this.set('params', params)
    if (points && points.length > 0) {
      this.setPoints(points)
    } else if (coordinates && coordinates.length > 0) {
      this.setCoordinates(coordinates)
    }
  }

  /**
   * 
   * @returns {*}
   */
  getPlotType () {
    return this.type
  }

  /**
   * 
   */
  generate () {
    let points = this.getPointCount()
    if (points < 2) {
      return false
    } else if (points === 2) {
      this.setCoordinates([this.points])
    } else {
      let pnts = this.getPoints()
      let [center, pnt2, pnt3] = [pnts[0], pnts[1], pnts[2]]
      let radius = PlotUtils.MathDistance(pnt2, center)
      let startAngle = PlotUtils.getAzimuth(pnt2, center)
      let endAngle = PlotUtils.getAzimuth(pnt3, center)
      let pList = PlotUtils.getArcPoints(center, radius, startAngle, endAngle)
      pList.push(center, pList[0])
      this.setCoordinates([pList])
    }
  }

  /**
   * 
   * @param map
   */
  setMap (map) {
    if (map && map instanceof ol.Map) {
      this.map = map
    } else {
      throw new Error('')
    }
  }

  /**
   * 
   * @returns {ol.Map|*}
   */
  getMap () {
    return this.map
  }

  /**
   * Plot
   * @returns {boolean}
   */
  isPlot () {
    return true
  }

  /**
   * 
   * @param value
   */
  setPoints (value) {
    this.points = !value ? [] : value
    if (this.points.length >= 1) {
      this.generate()
    }
  }

  /**
   * 
   * @returns {Array.<T>}
   */
  getPoints () {
    return this.points.slice(0)
  }

  /**
   * 
   * @returns {Number}
   */
  getPointCount () {
    return this.points.length
  }

  /**
   * 
   * @param point
   * @param index
   */
  updatePoint (point, index) {
    if (index >= 0 && index < this.points.length) {
      this.points[index] = point
      this.generate()
    }
  }

  /**
   * 
   * @param point
   */
  updateLastPoint (point) {
    this.updatePoint(point, this.points.length - 1)
  }

  /**
   * 
   */
  finishDrawing () {
  }
}

export default Sector



